@use "vars" as *;

#footer{
	background: #F8F9F9;
	padding: 150px 0 0;
	@include mq(){
		padding: 80px 0 0;
	}
	.footer-block{
		@include flex(0,0,wrap);
		margin-bottom: 65px;
		@include mq(){
			text-align: center;
			margin-bottom: 30px;
			.address-cusotm{
				line-height: 1.8;
				span{
					@include font-size(12);
				}
			}
		}
		&__note{
			width: 53%;
			@include fts(14,20);
			letter-spacing: 1px;
			padding: 0 5% 0 0;
			@include mq(){
				width: 100%;
				padding: 0;
				margin-bottom: 30px;
			}
			small{
				@include fts(12,21.6);
			}
		}
		&__contact{
			width: 47%;
			@include mq(){
				width: 100%;
			}
		}
	}
	.footer-logo{
		display: block;
		max-width: 280px;
		margin-bottom: 42px;
		@include mq(){
			max-width: 230px;
			margin: 0 auto 34px;
		}
	}
	.footer-item{
		margin-bottom: 48px;
		@include mq(){
			margin-bottom: 31px;
		}
		li{
			position: relative;
			font-size: 1.5rem;
			line-height: 1.6;
			padding: 0 0 0 45px;
			margin-bottom: 20px;
			@include mq(){
				padding: 0 0 0 35px;
			}
			&:last-child{
				margin-bottom: 0;
			}
			img{
				width: 24px;
				position: absolute;
				top: 0;
				left: 0;
			}
		}
	}
	.footer-calendar{
		width: 100%;
		@include font-size(16);
		letter-spacing: 1px;
		margin-bottom: 20px;
		@include mq(){
			@include font-size(13);
		}
		tr{
			border-bottom: 1px solid #E6ECEE;
			th,td{
				padding: 9px 0;
				text-align: center;
				&:nth-child(1){
					width: 170px;
					text-align: left;
					@include mq(){
						width: 90px;
						text-align: center;
					}
					span{
						display: inline-block;
						position: relative;
						@include mq(){
							display: block;
						}
						&:nth-child(1){
							padding: 0 10px 0 0;
							margin: 0 6px 0 0;
							@include mq(){
								padding: 0 0 5px;
								margin: 0 0 0;
							}
							&:after{
								content: "";
								width: 4px;
								height: 1px;
								background: $blue;
								position: absolute;
								@include v-align;
								right: 0;
								@include mq(){
									top: auto;
									transform: translateY(0);
									bottom: 0;
									width: 1px;
									height: 4px;
									left: 0;
									margin: 0 auto;
								}
							}
						}
					}
				}
			}
			th{
				font-weight: bold;
				@include mq(){
					&:nth-child(1){
						@include font-size(12);
					}
				}
			}
			td{
				font-weight: 500;
				width: 50px;
				span{
					display: block;
					text-align: center;
					span{
						display: block;
						width: 40px;
						margin: 0 auto;
						position: relative;
						@include font-size(12);
						&:nth-child(1){
							padding: 0 0 8px;
							&:after{
								content: "";
								position: absolute;
								bottom: 2px;
								width: 1px;
								height: 6px;
								left: 0;
								right: 0;
								margin: 0 auto;
								background: #23375f;
							}
						}
					}
				}
				.dots{
					@include font-size(33);
					@include mq(){
						@include font-size(25);
					}
				}
			}
		}
	}
	.footer-contact{
		background: white;
		text-align: center;
		@include font-size(15);
		letter-spacing: 1px;
		line-height: 1.8;
		padding: 64px 20px;
		@include mq(){
			padding: 39px 10px 50px;
		}
		&__ttl{
			display: block;
			@include font-size(20);
			font-weight: bold;
			color: $blue;
			margin-bottom: 40px;
			@include mq(){
				@include fts(16,23);
			}
		}
		p{
			margin-bottom: 39px;
			@include mq(){
				margin-bottom: 15px;
			}
		}
		&__tel{
			display: inline-block;
			line-height: 1;
			position: relative;
			text-align: left;
			padding: 0 0 0 75px;
			margin-bottom: 52px;
			&:before{
				content: "";
				width: 60px;
				height: 60px;
				border-radius: 50%;
				background: #E9EEEF;
				position: absolute;
				@include v-align;
				left: 0;
			}
			&:after{
				content: "";
				width: 28px;
				height: 28px;
				background: url("../img/common/icon-tel.png")no-repeat;
				background-size: 100%;
				position: absolute;
				@include v-align;
				left: 16px;
			}
			small,span{
				display: block;
			}
			small{
				@include font-size(14);
				color: #161616;
				margin-bottom: 9px;
			}
			span{
				@include font-size(32);
				color: $blue;
				font-weight: bold;
			}
		}
		.c-btn__02{
			max-width: 262px;
		}
	}
	.footer-maps{
		margin-bottom: 100px;
		@include mq(){
			margin-bottom: 76px;
		}
		iframe{
			width: 100%;
			height: 400px !important;
			@include mq(){
				height: 335px !important;
			}
		}
	}
	.footer-menu{
		background: white;
		@include flex(center,center,wrap);
		padding: 40px 15px;
		@include mq(sm){
			display: block;
			padding: 0;
			border: none;
		}
		li{
			padding: 0 10px;
			@include mq(sm){
				padding: 0;
			}
			a{
				display: block;
				@include font-size(14);
				color: #161616;
				position: relative;
				@include mq(sm){
					padding: 20px;
					&:after{
						content: "";
						border: solid #AFB0B3;
						border-width: 0 1px 1px 0;
						display: inline-block;
						padding: 3px;
						transform: rotate(-45deg);
						position: absolute;
						right: 15px;
						top: 26px;
					}
				}
			}
		}
	}
	.copyright{
		text-align: center;
		color: white;
		@include font-size(10);
		padding: 15px;
		background: $blue;
	}
}