@use "vars" as *;

#header{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 999;
	background: rgba(white,0.9);
	padding: 15px 40px;
	@include mq(tb){
		padding: 13px 20px;
		background: white;
	}
	.header-logo{
		display: block;
		width: 220px;
		@include mq(tb-min){
			position: absolute;
			@include v-align;
			left: 40px;
		}
		@include mq(tb){
			width: 140px;
		}
	}
	.header-tel{
		width: 60px;
		height: 60px;
		position: absolute;
		top: 0;
		right: 60px;
		background: #F2F7FA;
		display: none;
		img{
			width: 25px;
		}
		@include mq(tb){
			@include flex(center,center);
		}
	}
	.header-nav{
		@include mq(tb-min){
			display: flex;
			justify-content: flex-end;
			flex-wrap: wrap;
			align-content: center;
		}
		@include mq(tb){
			display: none;
			position: absolute;
			top: 60px;
			left: 0;
			right: 0;
			height: calc(100vh - 60px);
			overflow-y: scroll;
			background: white;
			&.active{
				display: block;
			}
		}
		&__tel{
			display: flex;
			justify-content: flex-end;
			align-items: flex-end;
			line-height: 1;
			width: 100%;
			@include font-size(12);
			margin-bottom: 11px;
			p{
				padding: 0 0 4px 12px;
			}
			a{
				display: block;
				@include font-size(22);
				color: $blue;
				font-weight: bold;
				padding: 0 0 0 40px;
				position: relative;
				&:before{
					content: "";
					width: 28px;
					height: 28px;
					border-radius: 50%;
					background: $gray;
					position: absolute;
					left: 0;
					@include v-align;
				}
				&:after{
					content: "";
					width: 13px;
					height: 13px;
					background: url("../img/common/icon-tel.png")no-repeat;
					background-size: 100%;
					position: absolute;
					left: 8px;
					@include v-align;
				}
			}
		}
		&__menu{
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-end;
			align-items: center;
			width: calc(100% - 255px);
			padding-right: 15px;
			@include mq(tb){
				display: block;
				width: 100%;
				padding-right: 0;
			}
			&-item{
				padding: 0 10px;
				@include mq(tb){
					padding: 0;
				}
				a{
					display: block;
					@include font-size(15);
					font-weight: 500;
					color: #161616;
					padding: 0 0 5px;
					position: relative;
					@include mq(tb){
						@include font-size(14);
						border-bottom: 1px solid #E6ECEE;
						padding: 20px;
						&:after{
							content: "";
							border: solid #AFB0B3;
							border-width: 0 1px 1px 0;
							display: inline-block;
							padding: 3px;
							transform: rotate(-45deg);
							position: absolute;
							right: 15px;
							top: 26px;
						}
					}

					@include mq(tb-min){
						&:after{
							content: "";
							position: absolute;
							bottom: 0;
							left: 0;
							right: 0;
							height: 1px;
							background: $blue;
							@include smooth-transition;
							opacity: 0;
							visibility: hidden;
						}
						&.active,&:hover{
							opacity: 1;
							color: $blue;
							&:after{
								opacity: 1;
								visibility: visible;
							}
						}
					}
				}
			}
		}
		&__btn{
			width: 255px;
			@include flex(between,center,wrap);
			.c-btn__01{
				max-width: 123px;
				height: 40px;
			}
		}
	}
	.header-contact{
		padding: 20px;
		&__box{
			background: #F8F9F9;
			text-align: center;
			@include fts(15.25);
			padding: 30px 10px;
			p{
				letter-spacing: 1px;
				margin-bottom: 30px;
			}
			.c-btn__02{
				width: 100%;
				max-width: 262px;
				margin-bottom: 20px;
				&:last-child{
					margin-bottom: 0;
				}
			}
		}
	}
	.js-mobile {
		width: 60px;
		height: 60px;
		position: absolute;
		right: 0;
		top: 0;
		cursor: pointer;
		z-index: 12;
		display: none;
		background: $blue;
		@include mq(tb){
			@include flex(center,center);
		}
		span{
			width: 25px;
			height: 2px;
			background: white;
			border-radius: 3px;
			position: relative;
			&:before,&:after{
				position: absolute;
				left: 0;
				width: 100%;
				height: 100%;
				background: white;
				content: '';
				-webkit-transition: -webkit-transform 0.3s;
				transition: transform 0.3s;
				border-radius: 3px;
			}
			&:before{
				-webkit-transform: translateY(-250%);
				transform: translateY(-250%);
				top: -3px;
			}
			&:after{
				-webkit-transform: translateY(250%);
				transform: translateY(250%);
				bottom: -3px;
			}
		}
		&--close {
			span{
				background-color: transparent;
				&:before,&:after{
					width: 100%;
					background: white;
				}
				&:before {
					-webkit-transform: translateY(0) rotate(45deg);
					transform: translateY(0) rotate(45deg);
					top: 2px;
				}
				&:after {
					-webkit-transform: translateY(0) rotate(-45deg);
					transform: translateY(0) rotate(-45deg);
					bottom: -2px;
				}
			}
		}
	}
	&.top-page{

		@include mq(tb-min){
			position: absolute;
			background: none;
			.header-logo{
				opacity: 0;
				visibility: hidden;
			}
			.header-nav__tel{
				outline: 0;
				visibility: hidden;
			}
			.header-nav__btn{
				display: none;
			}
		}
	}
}


.js-locked {
	overflow: hidden;
	-ms-touch-action: none;
	touch-action: none;
	body {
		-webkit-overflow-scrolling: auto;
	}
}