@use "vars" as *;

.mv-slider{
	.slick-arrow{
		display: none !important;
	}
	&__item{
		position: relative;
		width: 100%;
		height: 100vh;
		min-height: 750px;
		overflow: hidden;

		&.slick-current {
			-webkit-mask-image: radial-gradient(circle farthest-corner at 100% 0%, #000 60%, transparent 90%);
			-webkit-mask-repeat: no-repeat;
			-webkit-mask-size: 200% 100%;
			-webkit-mask-position: -100% 0;
			transition-property: -webkit-mask-position;
			transition-property: mask-position;
			transition-property: mask-position, -webkit-mask-position;
			transition-duration: 5s;
			transition-timing-function: cubic-bezier(0.3, 0.1, 0.1, 1);
			animation: hero_anime 2s forwards;
			.mv-slider__img{
				transform: scale(1.04);
			}
		}
		&.preve-slide{
			opacity: 1 !important;
		}
	}
	&__img{
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 0;
		background-position: center;
		background-size: cover;
		transition: 10s ease-in-out;
		&:after{
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			height: 80%;
			background: url("../img/index/bg-mv.png")no-repeat;
			background-size: 100%;
			background-position: bottom center;
			@include mq(){
				background: url("../img/index/bg-mv-sp.png")no-repeat;
				background-size: 100%;
				background-position: bottom center;
			}
		}
		&--01{
			background: url("../img/index/mv-01.jpg")no-repeat;
			background-size: cover;
			background-position: center;
		}
		&--02{
			background: url("../img/index/mv-02.jpg")no-repeat;
			background-size: cover;
			background-position: center;
		}
		&--03{
			background: url("../img/index/mv-03.jpg")no-repeat;
			background-size: cover;
			background-position: center;
		}
		&--04{
			background: url("../img/index/mv-04.jpg")no-repeat;
			background-size: cover;
			background-position: center;
		}
		&--05{
			background: url("../img/index/mv-05.jpg")no-repeat;
			background-size: cover;
			background-position: center;
		}
	}
}
.mv-heading{
	position: absolute;
	bottom: 68px;
	right: 50px;
	max-width: 475px;
	@include fts(16.28);
	color: white;
	letter-spacing: 1px;
	@include mq(){
		left: 20px;
		right: 20px;
		bottom: 42px;
	}
	&__ttl{
		display: block;
		@include fts(30,45);
		font-weight: bold;
		margin-bottom: 20px;
		@include mq(){
			@include fts(26,36);
			margin-bottom: 16px;
		}
	}
}
.mv-sidebar{
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	width: 282px;
	background: rgba(white,0.9);
	text-align: center;
	padding: 60px 15px 40px;
	display: flex;
	flex-wrap: wrap;
	align-content: space-between;
	@include mq(tb){
		display: none;
	}
	&__item{
		width: 100%;
	}
	&__note{
		@include font-size(14);
		color: #23375F;
		font-weight: 500;
		span{
			display: inline-block;
			position: relative;
			padding: 0 25px;
			margin-bottom: 30px;
			&:before,&:after{
				position: absolute;
				bottom: 0;
			}
			&:before{
				content: "＼";
				left: 0;
			}
			&:after{
				content: "／";
				right: 0;
			}
		}
	}
	&__logo{
		display: block;
		width: 160px;
		margin: 0 auto 30px;
	}
	&__ct{
		img{
			display: block;
			width: 46px;
			margin: 0 auto;
		}
	}
	&__tel{
		position: relative;
		@include font-size(13);
		padding: 35px 0 0;
		margin-bottom: 40px;
		&:before{
			content: "";
			width: 30px;
			height: 30px;
			border-radius: 50%;
			background: $gray;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			margin: 0 auto;
		}
		&:after{
			content: "";
			width: 15px;
			height: 15px;
			background: url("../img/common/icon-tel.png")no-repeat;
			background-size: 100%;
			position: absolute;
			top: 8px;
			left: 0;
			right: 0;
			margin: 0 auto;
		}
		span{
			display: block;
			@include font-size(24);
			font-weight: bold;
			color: $blue;
		}
	}
	&__btn{
		max-width: 210px;
		margin: 0 auto;
		.c-btn__01{
			margin-bottom: 20px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}


.sec-about{
	padding: 155px 0 250px;
	background: #F8F9F9;
	@include mq(){
		padding: 64px 0 110px;
	}
}
.about-block{
	@include flex(0,0,wrap);
	&__img{
		width: 51.5%;
		@include mq(){
			width: auto;
			margin: 0 0 28px 0;
		}
	}
	&__ct{
		width: 48.5%;
		padding: 17.5% 0 0 50px;
		letter-spacing: 1px;
		@include mq(){
			width: 100%;
			padding: 0;
		}
		&-ttl{
			display: block;
			margin-bottom: 20px;
			@include mq(){
				margin-bottom: 13px;
			}
			span{
				display: block;
			}
			.c-ft--en{
				color: $pink;
				@include font-size(40);
				line-height: 1.4;
				margin-bottom: 7px;
				@include mq(){
					@include font-size(32);
				}
			}
			.c-ft--jp{
				@include fts(26,44);
				font-weight: bold;
				@include mq(){
					@include fts(20,28);
				}
			}
		}
	}
}


.sec-features{
	background: white;
	border-radius: 100px 100px 0 0;
	margin: -100px 0 0;
	padding: 153px 0;
	@include mq(){
		border-radius: 50px 50px 0 0;
		margin: -50px 0 0;
		padding: 60px 0 80px;
	}
}
.features-list{
	@include flex(center,0,wrap);
	margin: 0 -20px 25px;
	@include mq(){
		&.js-features-list{
			display: block;
			.slick-list{
				padding: 0 6% !important;
			}
			.slick-slide{
				padding: 0 5px;
			}
			.slick-arrow{
				display: none !important;
			}
			.slick-dots{
				position: static;
				@include flex(center,center);
				padding: 10px 0 0;
				li{
					height: 10px;
					width: 10px;
					button{
						width: 10px;
						height: 10px;
						border-radius: 50%;
						border: none;
						background: $pink;
						&:before{
							display: none;
						}
					}
					&.slick-active{
						button{
							background: $blue;
						}
					}
				}
			}
		}
	}
	&__item{
		width: 33.333%;
		padding: 0 20px;
		margin-bottom: 40px;
		@include mq(){
			padding: 0 5px;
			margin-bottom: 0;
		}
	}
	&__box{
		border: 1px solid #E6ECEE;
		height: 100%;
	}
	&__img{
		img{
			width: 100%;
		}
	}
	&__ct{
		@include fts(15,25);
		letter-spacing: 1px;
		padding: 20px 27px 29px;
		@include mq(){
			padding: 20px 20px 29px;
		}
		&-count{
			display: block;
			text-align: center;
			font-weight: bold;
			color: $blue;
			margin-bottom: 5px;
		}
		&-ttl{
			@include flex(center,center,wrap);
			@include fts(18,26);
			text-align: center;
			font-weight: bold;
			min-height: 52px;
			margin-bottom: 13px;
		}
	}
}


.sec-service{
	background: #F8F9F9;
	padding: 152px 0;
	@include mq(){
		padding: 83px 0;
	}
}

.service-list{
	@include flex(center,0,wrap);
	margin: 0 -15px 30px;
	@include mq(){
		margin: 0 -5px 30px;
	}
	&__item{
		width: 20%;
		padding: 0 15px;
		margin-bottom: 30px;
		@include mq(tb){
			width: 25%;
		}
		@include mq(){
			width: 33.333%;
			padding: 0 5px;
			margin-bottom: 10px;
		}
		@include mq(sm){
			width: 50%;

		}
	}
	&__box{
		display: block;
		background: white;
		border-radius: 20px;
		box-shadow: 0 0 15px 0 rgba(#27292B,0.08);
		height: 100%;
		padding: 41px 30px 20px;
		min-height: 233px;
		@include smooth-transition;
		position: relative;
		@include mq(){
			min-height: 193px;
			padding: 30px 15px 20px;
		}
		&:hover{
			box-shadow: 0 0 0 0 rgba(#27292B,0.08);
			translate: 0 5px;
			border: 1px solid #e6ecee;
		}
	}
	&__img{
		width: 90px;
		height: 90px;
		margin: 0 auto 20px;
		@include mq(){
			width: 80px;
			height: 80px;
			margin: 0 auto 15px;
		}
	}
	&__ct{
		text-align: center;
		&-ttl{
			display: block;
			span{
				display: block;
			}
			.c-ft--jp{
				@include font-size(16);
				font-weight: bold;
				color: $blue;
				margin-bottom: 4px;
				@include mq(){
					@include font-size(14);
				}
			}
			.c-ft--en{
				@include font-size(10);
				font-family: $font-jp;
				font-weight: 500;
				color: $pink;
			}
		}
	}
}

.sec-greeting{
	padding: 155px 0;
	@include mq(){
		padding: 80px 0;
	}
}
.greeting-block{
	@include flex(0,end,wrap);
	&__ct{
		width: 48%;
		letter-spacing: 1px;
		padding: 0 5% 0 0;
		@include mq(){
			width: 100%;
			padding: 0;
			order: 2;
		}
		&-ttl{
			display: block;
			margin-bottom: 15px;
			@include mq(){
				margin-bottom: 10px;
			}
			span{
				display: block;
			}
			.c-ft--en{
				color: $pink;
				@include font-size(40);
				line-height: 1;
				margin-bottom: 15px;
				@include mq(){
					@include font-size(32);
				}
			}
			.c-ft--jp{
				@include fts(26,44);
				font-weight: bold;
				@include mq(){
					@include fts(20,28);
				}
			}
		}
		&-name{
			font-weight: 500;
			letter-spacing: 3px;
			text-align: right;
			@include mq(){
				@include font-size(16);
				letter-spacing: 1px;
			}
		}
		p{
			margin-bottom: 30px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	&__img{
		width: 52%;
		padding: 0 0 77px;
		@include mq(){
			order: 1;
			width: auto;
			padding: 0;
			margin: 0 0 33px 0;
		}
	}
}

.sec-news{
	background: #F8F9F9;
	padding: 150px 0;
	@include mq(){
		padding: 80px 0;
	}
}
.news-list{
	margin-bottom: 60px;
	@include fts(14,20);
	@include mq(){

		margin-bottom: 28px;
	}
	&__item{
		margin-bottom: 18px;
		&:last-child{
			margin-bottom: 0;
		}
		a{
			@include flex(0,0,wrap);
			color: #161616;
			border-bottom: none;
			position: relative;
			letter-spacing: 1px;
			padding: 28px 43px 23px 35px;
			border-radius: 10px;
			background: white;
			box-shadow: 0 0 15px 0 rgba(#27292B,0.08);
			@include mq(){
				padding: 21px 23px 18px 21px;
			}
			&:after{
				content: "";
				width: 30px;
				height: 31px;
				background: url("../img/common/arrow-right--gray.png")no-repeat;
				background-size: 100%;
				position: absolute;
				right: 15px;
				@include v-align;
				@include smooth-transition;
				@include mq(){
					display: none;
				}
			}
			span{
				display: block;
				color: #23375F;
				position: relative;
				@include mq(){
					@include font-size(12);
				}
				&:nth-child(1){
					padding: 0 18px 0 0;
					margin: 0 12px 0 0;
					@include mq(){
						padding: 0 8px 0 0;
						margin: 0 8px 0 0;
					}
				}
			}
			p{
				width: 100%;
				padding: 7px 0 0 0;
				overflow: hidden;
				text-overflow: ellipsis;
				@include mq(md-min){
					white-space: nowrap;
				}
				@include mq(){
					-webkit-line-clamp: 3;
					height: 70px;
					display: -webkit-box;
					-webkit-box-orient: vertical;
				}
			}
			&:hover{
				opacity: 1;
				box-shadow: 0 0 0 0 rgba(#27292B,0.08);
				&:after{
					background: url("../img/common/arrow-right--blue.png")no-repeat;
					background-size: 100%;

				}
			}
		}
	}
}

.sec-question{
	padding: 150px 0;
	@include mq(){
		padding: 80px 0;
		.heading-note{
			letter-spacing: 0;
		}
	}
}


.question-list{
	@include flex(0,0,wrap);
	margin: 0 -20px 75px;
	@include mq(){
		margin: 0 -20px 30px;
	}
	&__item{
		width: 33.333%;
		padding: 0 20px;
		position: relative;
		@include mq(){
			width: 100%;
			margin-bottom: 30px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	.c-txt__note{
		width: 100%;
		@include mq(){
			padding: 0 20px;
			letter-spacing: 1px;
			margin: -20px 0 30px;
		}
	}
	&__box{
		border: 1px solid #E6ECEE;
		text-align: center;
		position: relative;
		height: 100%;
		letter-spacing: 1px;
		padding: 44px 20px 33px;
		@include mq(){
			padding: 29px 20px 20px;
			letter-spacing: 0;
		}
		>*{
			position: relative;
			z-index: 2;
		}
		&:before,&:after{
			content: "";
			position: absolute;
			top: -1px;
			bottom: -1px;
			width: 20%;
			background: white;
		}
		&:before{
			left: 15%;
		}
		&:after{
			left: 65%;
		}
		&-icon{
			width: 40px;
			margin: 0 auto 15px;
			position: static;
			@include mq(){
				width: 30px;
				margin: 0 auto 10px;
			}
			&:before{
				content: "";
				height: 50%;
				position: absolute;
				@include v-align;
				left: -1px;
				right: -1px;
				background: white;
			}
			img{
				position: relative;
				z-index: 1;
			}
		}
		&-ttl{
			display: block;
			@include font-size(16);
			font-weight: 500;
			margin-bottom: 10px;
		}
		p{
			margin-bottom: 15px;
			@include mq(){
				margin-bottom: 7px;
			}
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}
