@use "vars" as *;


.c-fc{ //font-color
	&--white{
		color: white !important;
	}
	&--black{
		color: black !important;
	}
	&--red{
		color: #B78A4D !important;
	}
	&--blue{
		color: $blue !important;
	}
}



.c-ft{//font-family
	&--en{
		font-family: $font-en;
	}
	&--jp{
		font-family: $font-jp;
	}
}






.c-al{
	&--c{
		text-align: center !important;
	}
	
	&--r{
		text-align: right !important
	}
	
	&--l{
		text-align: left !important;
	}
}



.pull-left{
	margin-left: calc((100vw - 193%) / 2* -1);
	@include mq(tb){
		margin-left: -20px;
	}
}
.pull-right{
	margin-right: calc((100vw - 193%) / 2* -1);
	@include mq(tb){
		margin-right: -20px;
	}
}






/* ====================================================
Component
==================================================== */
// Common Text style
.c-txt{
	&__basic{
		@include font-size(16);
		line-height: 2;
		@include mq(){
			@include fts(15,27);
		}
	}
}




//ttl
.c-ttl{
	&__01{
		display: block;
		margin-bottom: 57px;
		@include mq(){
			padding: 0 0 0 30px;
			margin-bottom: 32px;
		}
		span{
			display: block;
			position: relative;
			line-height: 1.2;
		}
		.c-ft--jp{
			color: $blue;
			@include font-size(40);
			font-weight: bold;
			letter-spacing: 2px;
			margin-bottom: 15px;
			@include mq(){
				@include font-size(24);
				margin-bottom: 12px;
			}
			&:before{
				content: "";
				width: 80px;
				height: 2px;
				background: $blue;
				position: absolute;
				@include v-align;
				right: calc(100% + 40px);
				@include mq(){
					right: calc(100% + 20px);
					top: 18px;
					transform: translateY(0);
				}
			}
		}
		.c-ft--en{
			font-family: $font-jp;
			color: $pink;
			@include font-size(16);
			font-weight: 500;
			@include mq(){
				@include font-size(12);
			}
		}
	}
	&__02{
		display: block;
		text-align: center;
		color: $blue;
		@include font-size(36);
		line-height: 1.4;
		font-weight: bold;
		margin-bottom: 61px;
		@include mq(){
			@include font-size(24);
			margin-bottom: 49px;
		}
	}
}

//btn
.c-btn{
	&__01{
		width: 100%;
		max-width: 210px;
		height: 50px;
		@include flex(center,center,wrap);
		text-align: center;
		border-radius: 50px;
		border: 1px solid $blue;
		outline: none;
		cursor: pointer;
		@include smooth-transition;
		@include font-size(14);
		line-height: 1.3;
		font-weight: 500;
		background: white;
		&--blue{
			background: $blue;
			color: white;
		}
	}
	&__02{
		width: auto;
		height: 60px;
		margin: 0 auto;
		border-radius: 60px;
		border: 1px solid $blue;
		@include font-size(15);
		color: $blue;
		text-align: center;
		@include flex(center,center,wrap);
		position: relative;
		letter-spacing: 1px;
		padding: 0 60px 0 40px;
		&:after{
			content: "";
			width: 35px;
			height: 36px;
			background: url("../img/common/arrow-right--blue.png")no-repeat;
			background-size: 100%;
			position: absolute;
			@include v-align;
			right: 10px;
		}
		&--blue{
			color: white;
			background: $blue;
			&:after{
				background: url("../img/common/arrow-right--white.png")no-repeat;
				background-size: 100%;
			}
		}
		&--tel{
			@include font-size(24);
			font-weight: bold;
			padding: 0 0 0 30px;
			&:after{
				display: none;
			}
			&:before{
				content: "";
				width: 35px;
				height: 35px;
				position: absolute;
				@include v-align;
				left: 10px;
				background: url("../img/common/icon-tel-02.png")no-repeat;
				background-size: 100%;
			}
		}
		&:hover{
			opacity: 1;
			color: white;
			background: $blue;
			&:after{
				background: url("../img/common/arrow-right--white.png")no-repeat;
				background-size: 100%;
			}
		}
	}
}


//fade up
.js-scrollin {
	opacity: 0;
	-webkit-transform: translate(0, 70px);
	-ms-transform: translate(0, 70px);
	transform: translate(0, 70px);
	&.active{
		opacity: 1;
		-webkit-transform: translate(0, 0);
		-ms-transform: translate(0, 0);
		transform: translate(0, 0);
		-webkit-transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
		-o-transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
		transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	}
}



