@use "vars" as *;


.inner {
	display: block;
	max-width: 1240px;
	padding: 0 20px;
	margin: 0 auto;
}

.heading-note{
	line-height: 1.875;
	letter-spacing: 1px;
	margin-bottom: 60px;
	@include mq(){
		line-height: 1.8;
		margin-bottom: 30px;
	}
}

.writing-block{
	@include flex(0,0,wrap);
	&__heading{
		width: 260px;
		@include mq(){
			width: 100%;
		}
	}
	&__ct{
		width: calc(100% - 260px);
		@include mq(){
			width: 100%;
		}
	}
}
.flex-end{
	@include flex(end,center,wrap);
	@include mq(){
		justify-content: center;
	}
	.c-btn__02{
		margin: 0;
	}
}
.flex-center{
	@include flex(center,center,wrap);
	.c-btn__02{
		margin: 0 15px;
		@include mq(){
			margin-bottom: 20px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}

@keyframes hero_anime {
	0% {
		-webkit-mask-position: -100% 0;
	}
	100% {
		-webkit-mask-position: 100% 0;
	}
}
